const deleteStyle = {
  marginLeft: 50,
  display: "block",
  borderRadius: 50,
  marginTop: -15,
  fontSize: "12px",
  height: "20px",
  width: "20px",
  color: "pink"
}
const List = props => (
  <ul>
    {
      props.items.map((item, index) => <li key={index}>{item}<span style={deleteStyle} onClick={()=> props.deleteItem(index, item)}>X</span></li>)
    }
  </ul>
);

export default List;